import { RootAPI, APIResponse } from "./model/API";
import { ServerRequestHeader, ServerResponse } from "./model/Server";
import {
  ICoinsId,
  IGlobal,
  ICoinsIdMarketChart,
  ICoinIdStatusUpdates,
  IPing,
  ISimplePrice,
  IStatusUpdates,
  ISimpleSupportedVsCurrencies,
  ICoinsList,
  IExchangeRates,
  ICoinsMarket,
  IExchanges,
  IExchangesListItem,
  IExchangesId,
  IExchangesIdTicker,
  ICoinTickers,
  IDerivatives,
  IDerivativesExchanges,
  IDerivativesExchangesId,
  IFinanceProduct,
  IFinancePlatform,
  ICoinsLite,
} from "./model/APIResponse";
import {
  BeamFilterCategories,
  BeamFilterProjectType,
  SortOrder,
  CoinsMarketsSortOrder,
  PriceChangePercentage,
  TickerSortOrder,
  DerivativesExchangesSortType,
  DerivativesExchangeTickerType,
} from "./model/type";
import { urlEncode, timeoutWrapper, handleError } from "./utils/functions";

export class CoinGeckoAPI {
  baseUrl: string;
  fetch: (input: RequestInfo, init?: RequestInit) => Promise<Response>;
  timeout: number;
  getHeader: ServerRequestHeader;
  postHeader: ServerRequestHeader;

  constructor(
    fetch: (input: RequestInfo, init?: RequestInit) => Promise<Response>,
    timeout = 20000,
    baseUrl: string = RootAPI.PROD,
    getHeader: ServerRequestHeader = {},
    postHeader: ServerRequestHeader = {}
  ) {
    this.fetch = fetch;
    this.timeout = timeout;
    this.baseUrl = baseUrl;
    this.getHeader = getHeader;
    this.postHeader = postHeader;
  }

  /**
   * Using Fetch to send GET request
   */
  async getReq<T>(
    endpoint: string,
    header: ServerRequestHeader = {}
  ): APIResponse<T> {
    const response = await timeoutWrapper(
      this.timeout,
      this.fetch(`${this.baseUrl}${endpoint}`, {
        method: "GET",
        headers: {
          ...this.getHeader,
          ...header,
        },
      })
    );
    handleError(response);
    let data = null;
    try {
      data = await response.json();
    } catch (_err) {
      return {
        response,
        data,
        endpoint,
      };
    }
    return {
      response,
      data,
      endpoint,
    };
  }

  /**
   * Using Fetch to send POST request
   */
  async postReq<T>(
    endpoint: string,
    body: any,
    header: ServerRequestHeader = {}
  ): APIResponse<T> {
    const response = await this.fetch(`${this.baseUrl}${endpoint}`, {
      method: "POST",
      body,
      headers: {
        ...this.postHeader,
        ...header,
      },
    });
    handleError(response);
    let data = null;
    try {
      data = await response.json();
    } catch (_err) {
      return {
        response,
        data,
        endpoint,
      };
    }
    return {
      response,
      data,
      endpoint,
    };
  }

  /**
   * Using Fetch to send DELETE request
   */
  async deleteReq<T>(
    endpoint: string,
    header: ServerRequestHeader = {}
  ): APIResponse<T> {
    const response = await timeoutWrapper(
      this.timeout,
      this.fetch(`${this.baseUrl}${endpoint}`, {
        method: "DELETE",
        headers: {
          ...this.getHeader,
          ...header,
        },
      })
    );
    handleError(response);
    let data = null;
    try {
      data = await response.json();
    } catch (_err) {
      return {
        response,
        data,
        endpoint,
      };
    }
    return {
      response,
      data,
      endpoint,
    };
  }

  /**
   * Using Fetch to send PUT request
   */
  async putReq<T>(
    endpoint: string,
    body: any,
    header: ServerRequestHeader = {}
  ): APIResponse<T> {
    const response = await this.fetch(`${this.baseUrl}${endpoint}`, {
      method: "PUT",
      body,
      headers: {
        ...this.postHeader,
        ...header,
      },
    });
    handleError(response);
    let data = null;
    try {
      data = await response.json();
    } catch (_err) {
      return {
        response,
        data,
        endpoint,
      };
    }
    return {
      response,
      data,
      endpoint,
    };
  }

  // API CALL

  /**
   * Check API server status
   * https://api.coingecko.com/api/v3/ping
   */
  async getPing(): ServerResponse<boolean> {
    const d = await this.getReq<IPing>("/ping");
    return {
      ...d,
      data: d.data.gecko_says === "(V3) To the Moon!",
    };
  }

  /**
   * Get the current price of any cryptocurrencies in any other supported currencies that you need.
   * https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=usd
   */
  getSimplePrice(
    ids: string[],
    vsCurrencies: string[]
  ): ServerResponse<ISimplePrice> {
    const encodedUrl = urlEncode({ ids, vs_currencies: vsCurrencies });
    return this.getReq<ISimplePrice>(`/simple/price?${encodedUrl}`);
  }

  // TODO: /simple/token_price/{id} Get current price of tokens (using contract addresses) for a given platform in any other currency that you need.

  /**
   * Get list of supported_vs_currencies.
   * https://api.coingecko.com/api/v3/simple/supported_vs_currencies
   */
  getSimpleSupportedVsCurrencies(): ServerResponse<
    ISimpleSupportedVsCurrencies
  > {
    return this.getReq<ISimpleSupportedVsCurrencies>(
      "/simple/supported_vs_currencies"
    );
  }

  /**
   * List all supported coins id, name and symbol (no pagination required)
   * https://api.coingecko.com/api/v3/coins/list
   */
  getCoinsList(): ServerResponse<ICoinsList> {
    return this.getReq<ICoinsList>("/coins/list");
  }

  // GET /coins/markets List all supported coins price, market cap, volume, and market related data

  getCoinsMarket(
    vsCurrency: string,
    order: CoinsMarketsSortOrder = CoinsMarketsSortOrder.MARKET_CAP_DESC,
    ids: string[] = [],
    perPage = 100,
    page = 1,
    sparkline = false,
    priceChangePercentage: PriceChangePercentage[] = []
  ) {
    if (
      perPage &&
      typeof perPage === "number" &&
      (perPage > 250 || perPage < 1)
    ) {
      throw new Error("Invalid per page, it should be within 1 to 250.");
    }
    const encodedUrl = urlEncode({
      vs_currency: vsCurrency,
      order,
      ids: ids.join(","),
      per_page: perPage,
      page,
      sparkline,
      price_change_percentage: priceChangePercentage.join(","),
    });
    return this.getReq<ICoinsMarket>(`/coins/markets?${encodedUrl}`);
  }

  /**
   * Get current data (name, price, market, ... including exchange tickers) for a coin
   * https://api.coingecko.com/api/v3/coins/bitcoin
   */
  getCoinsId(
    coin: string,
    localization = true,
    tickers = true,
    marketData = true,
    communityData = true,
    developerData = true,
    sparkline = false,
    include_exchange_logo = false
  ): ServerResponse<ICoinsId> {
    const encodedUrl = urlEncode({
      localization,
      tickers,
      market_data: marketData,
      community_data: communityData,
      developer_data: developerData,
      sparkline,
      include_exchange_logo,
    });
    return this.getReq<ICoinsId>(`/coins/${coin}?${encodedUrl}`);
  }

  getCoinsIdTickers(
    id: string,
    includeExchangeLogo = true,
    page = 1,
    order = TickerSortOrder.TRUST_SCORE_DESC,
    exchangesIds: string[] = []
  ) {
    const encodedUrl = urlEncode({
      include_exchange_logo: includeExchangeLogo,
      page,
      order,
      exchanges_ids: exchangesIds.join(","),
    });
    return this.getReq<ICoinTickers>(`/coins/${id}/tickers?${encodedUrl}`);
  }

  /**
   * Get market chart of a certain coin (prices, market cap and volume)
   * https://api.coingecko.com/api/v3/coins/bitcoin/market_chart?vs_currency=usd&days=2
   */
  getCoinsIdMarketChart(
    coin: string,
    vsCurrency: string,
    days: number | string
  ): ServerResponse<ICoinsIdMarketChart> {
    const encodedUrl = urlEncode({
      vs_currency: vsCurrency,
      days,
    });
    return this.getReq<ICoinsIdMarketChart>(
      `/coins/${coin}/market_chart?${encodedUrl}`
    );
  }

  /**
   * Get status updates for a given coin
   * https://api.coingecko.com/api/v3/coins/dash/status_updates?page=1&per_page=2
   */
  getCoinsIdStatusUpdates(
    coin: string,
    page: number,
    perPage = 10
  ): ServerResponse<ICoinIdStatusUpdates> {
    const encodedUrl = urlEncode({
      per_page: perPage,
      page,
    });
    return this.getReq<ICoinIdStatusUpdates>(
      `/coins/${coin}/status_updates?${encodedUrl}`
    );
  }

  /**
   * List all the available category list
   * https://api.coingecko.com/api/v3/coins/category_list
   * @param locale
   */
  getCoinsCategoryList(locale = "en") {
    const encodedUrl = urlEncode({
      locale,
    });
    return this.getReq<{ category_id: string; name: string }[]>(
      `/coins/category_list?${encodedUrl}`
    );
  }

  getCoinsLite(
    vsCurrency = "usd",
    categoryId = "cryptocurrency",
    locale = "en",
    page = 1,
    perPage = 20,
    order = SortOrder.MARKET_CAP_DESCENDING
  ) {
    const encodedUrl = urlEncode({
      vs_currency: vsCurrency,
      category_id: categoryId,
      locale,
      page,
      per_page: perPage,
      order,
    });
    return this.getReq<ICoinsLite[]>(`/coins_lite?${encodedUrl}`);
  }

  /**
   * List all exchanges
   * https://api.coingecko.com/api/v3/exchanges
   */
  getExchanges(page = 1, perPage = 100) {
    const encodedUrl = urlEncode({
      per_page: perPage,
      page,
    });
    return this.getReq<IExchanges[]>(`/exchanges?${encodedUrl}`);
  }

  /**
   * List all supported markets id and name (no pagination required)
   * https://api.coingecko.com/api/v3/exchanges/list
   */
  getExchangesList() {
    return this.getReq<IExchangesListItem>(`/exchanges/list`);
  }

  /**
   * Get exchange volume in BTC and tickers
   * https://api.coingecko.com/api/v3/exchanges/binance
   */
  getExchangesId(id: string, tickers = true) {
    const encodedUrl = urlEncode({
      include_tickers: tickers,
    });
    return this.getReq<IExchangesId>(`/exchanges/${id}?${encodedUrl}`);
  }

  getExchangesIdTicker(
    id: string,
    coinIds: string[] = [],
    exchangeLogo = true,
    page = 0,
    order: TickerSortOrder = TickerSortOrder.TRUST_SCORE_DESC
  ) {
    const encodedUrl = urlEncode({
      coin_ids: coinIds.join(","),
      include_exchange_logo: exchangeLogo,
      page,
      order,
    });
    return this.getReq<IExchangesIdTicker>(
      `/exchanges/${id}/tickers?${encodedUrl}`
    );
  }

  /**
   * Get volume_chart data for a given exchange
   * https://api.coingecko.com/api/v3/exchanges/binance/volume_chart?days=1
   */
  getExchangesIdVolumeChart(id: string, days: number) {
    return this.getReq<Array<[number, number]>>(
      `/exchanges/${id}/volume_chart?days=${days}`
    );
  }

  /**
   * List all derivative tickers
   * https://api.coingecko.com/api/v3/derivatives
   */
  getDerivatives() {
    return this.getReq<IDerivatives[]>(`/derivatives`);
  }

  /**
   * List all derivative exchanges
   * https://api.coingecko.com/api/v3/derivatives/exchanges
   */
  getDerivativesExchanges(
    order = DerivativesExchangesSortType.OPEN_INTEREST_BTC_DESC
  ) {
    const encodedUrl = urlEncode({ order });
    return this.getReq<IDerivativesExchanges[]>(
      `/derivatives/exchanges?${encodedUrl}`
    );
  }

  /**
   * Show derivative exchange data
   * https://api.coingecko.com/api/v3/derivatives/exchanges?include_tickers=true
   * @param {string} id Derivative Exchange ID
   * @param {DerivativesExchangeTickerType} includeTickers (use DerivativesExchangeTickerType enum) "all" for all tickers, "unexpired" for unexpired tickers and "" to return no tickers
   */
  getDerivativesExchangesId(
    id: string,
    includeTickers = DerivativesExchangeTickerType.NONE
  ) {
    const encodedUrl =
      includeTickers !== DerivativesExchangeTickerType.NONE
        ? urlEncode({ include_tickers: includeTickers })
        : "";
    return this.getReq<IDerivativesExchangesId>(
      `/derivatives/exchanges/${id}?${encodedUrl}`
    );
  }

  /**
   * BEAM
   * List all status_updates with data (description, category, created_at, user, user_title and pin)
   * https://api.coingecko.com/api/v3/status_updates
   */
  getStatusUpdates(
    page: number,
    category: BeamFilterCategories = BeamFilterCategories.ALL_CATEGORIES,
    project: BeamFilterProjectType = BeamFilterProjectType.ALL,
    perPage = 10
  ): ServerResponse<IStatusUpdates> {
    const encodedUrl = urlEncode({
      page,
      category,
      project_type: project,
      per_page: perPage,
    });
    return this.getReq<IStatusUpdates>(`/status_updates?${encodedUrl}`);
  }

  getExchangeRates(): ServerResponse<IExchangeRates> {
    return this.getReq<IExchangeRates>("/exchange_rates");
  }

  /**
   * List all finance platforms
   * https://api.coingecko.com/api/v3/finance_platforms
   */
  getFinancePlatforms(perPage = 100, page = 1) {
    const encodedUrl = urlEncode({
      per_page: perPage,
      page,
    });
    return this.getReq<IFinancePlatform[]>(`/finance_platforms?${encodedUrl}`);
  }

  /**
   * List all finance products
   * https://api.coingecko.com/api/v3/finance_products
   */
  getFinanceProducts(perPage = 100, page = 1) {
    const encodedUrl = urlEncode({
      per_page: perPage,
      page,
    });
    return this.getReq<IFinanceProduct[]>(`/finance_products?${encodedUrl}`);
  }

  /**
   * Get cryptocurrency global data
   * https://api.coingecko.com/api/v3/global
   */
  getGlobal(): ServerResponse<IGlobal> {
    return this.getReq<IGlobal>("/global");
  }
}
